import React, { useState, useEffect } from 'react';
import { withRouter, Link } from 'react-router-dom';
import { useSelector, connect } from 'react-redux';
import { Form, Table, Spinner, OverlayTrigger, Popover, Button } from 'react-bootstrap';

import BootstrapSwitchButton from 'bootstrap-switch-button-react';

import AceEditor from "react-ace";
import "ace-builds/src-noconflict/theme-ambiance.js";
import "ace-builds/src-noconflict/mode-zencode";
import "ace-builds/src-noconflict/mode-json5";

import './UserProfile.css';
import { loadContractsUri } from '../../constants/api';
import * as actions from '../../store/actions/index';

import { hasJsonStructure } from '../../helpers/jsonHelper';

const PopoverContent = props => {
    return (
        <OverlayTrigger
            trigger="click"
            key={'bottom'}
            placement={'bottom'}
            overlay={
                <Popover id={`popover-positioned-bottom`}>
                    {/* <Popover.Title as="h3">{`Zencode`}</Popover.Title> */}
                    <div className={'d-flex justify-content-between popover-header'}>
                        <h5>{props.heading}</h5>
                        <button type="button" className="btn btn-primary btn-sm">Save</button>
                    </div>
                    <Popover.Content>
                        <AceEditor
                            mode={props.mode}
                            theme="ambiance"
                            onChange={() => { }}
                            name="UNIQUE_ID_OF_DIV"
                            showPrintMargin={false}
                            width='500px'
                            value={props.mode === 'zencode' ? props.content : (props.content && hasJsonStructure(props.content)) ? JSON.stringify(JSON.parse(props.content), null, '\t') : props.content}
                        />
                    </Popover.Content>
                </Popover>
            }
        >
            {props.name
                ? (<a href="#">{props.name}</a>)
                : props.content
                    ? (<a href="#">show</a>)
                    : (<a href="#" style={{ color: 'red' }}>empty</a>)
            }

        </OverlayTrigger>
    );
}


const UserProfile = props => {
    const { userContracts } = useSelector(state => state.zenroom);
    const [tableEmpty, setTableEmpty] = useState(true);

    const loadContract = (name, zencode, keys, data, config, index) => {
        props.onLoadContract({name, zencode, keys, data, config});
        props.onChangeUserLoaded(true);
        props.onSelectedIndex(index);
        props.history.push("/")


    }

    useEffect(() => {
        props.onChangeUserLoaded(false);
        props.onSelectedIndex(null);
        if (!props.contracts) {
            props.onLoadContracts();
        } else {
            setTableEmpty();
        }
    }, []);

    useEffect(() => {

        if (!props.contracts.length) {
            props.onLoadContracts();
        } else {
            setTableEmpty(false);
        }
    }, [props.contracts]);

    return (
        <div className={'pb-5 user-contracts pt-5'}>
            {/* <h4 className={'mt-5'}>Saved Contracts</h4> */}

            {tableEmpty
                ? (<div>
                    <Spinner animation="grow" variant="success" />
                    <Spinner animation="grow" variant="danger" />
                    <Spinner animation="grow" variant="warning" />
                </div>)
                : (<Table responsive>
                    <thead>
                        <tr>
                            <th></th>
                            <th>Smart contract</th>
                            <th>Parameter (-a)</th>
                            <th>Parameter (-k)</th>
                            <th>Config (-c)</th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {props.contracts && props.contracts.map((contract, index) => {
                            return (<tr key={index}>
                                <td><Form.Check aria-label="option 1" /></td>
                                <td>
                                    <PopoverContent
                                        name={contract.db.name}
                                        content={contract.file}
                                        mode={'zencode'}
                                        heading={'Zencode'}
                                        showTitle={false}
                                    />
                                </td>
                                <td>
                                    <PopoverContent
                                        content={contract.db.data}
                                        mode={'json5'}
                                        heading={'Data'}
                                    />
                                </td>
                                <td>
                                    <PopoverContent
                                        content={contract.db.keys}
                                        mode={'json5'}
                                        heading={'Keys'}
                                    />
                                </td>
                                <td>
                                    <PopoverContent
                                        content={contract.db.config}
                                        mode={'json5'}
                                        heading={'Data'}
                                    />
                                </td>
                                <td>
                                    <a onClick={() => loadContract(contract.db.name, contract.file, contract.db.keys, contract.db.data, contract.db.config, index )} style={{color: 'black', cursor: 'pointer'}}>Edit</a>
                                </td>
                                <td>
                                    {/* <button type="button" className="btn btn-outline-dark" style={{ height: '1.375rem', lineHeight: '0.5', padding: '.375rem .55rem', marginRight: '2px' }}>Edit</button>
                                    <button type="button" className="btn btn-outline-dark" style={{ height: '1.375rem', lineHeight: '0.5', padding: '.375rem .55rem' }}>Swagger</button> */}
                                    <BootstrapSwitchButton
                                        onlabel='ON'
                                        offlabel='OFF'
                                        checked={true}
                                        onstyle="outline-success"
                                        offstyle="outline-danger"
                                        height='3'
                                        size='xs'
                                        offstyle="outline-info" />
                                </td>
                            </tr>)
                        })
                        }
                    </tbody>
                </Table>)
            }

        </div >
    );

}

const mapStateToProps = state => {
    return {
        contracts: state.collections.contractCollection,
    };
}

const mapDispatchToProps = dispatch => {
    return {
        // onLoadContracts: () => dispatch(actions.loadContracts())
        onLoadContracts: () => dispatch(actions.loadContracts()),
        onLoadContract: (contract) => dispatch(actions.changeContract(contract)),
        onChangeUserLoaded: (loaded) => dispatch(actions.changeUserLoaded(loaded)),
        onSelectedIndex: (index) => dispatch(actions.changeSelectedIndex(index))
    }
}


export default withRouter(connect(mapStateToProps, mapDispatchToProps)(UserProfile));